@mixin fullBackground {
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

@mixin bgMiddle {
    background: {
        position: 50% 50%;
        repeat: no-repeat;
    }
}

@mixin bgCover($img: false) {
    @include bgMiddle;
    background: {
        size: cover;

        @if $img and $img != none {
            image: url($img);
        }
    }
}
